@media (max-width: 768px) {
  .nav {
    background-color: rgba(229, 229, 229, 0.9607843137);
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(-100%);
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.4s ease-in;
    overflow: auto;
    /* Hide navigation */
    /* Allows NO transitions at all */
    /* display: none; */
    /* 1) Hide it visually */
    opacity: 0;
    /* 2) Make it unaccessible to mouse and keyboard */
    pointer-events: none;
    /* 3) Hide it from screen readers */
    visibility: hidden;
  }
  .nav.open {
    opacity: 1;
    pointer-events: auto;
    visibility: visible;
    transform: translateX(0);
  }
  .nav__list {
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
  }
  .nav__list-item {
    font-size: 30px;
  }
  .burger {
    display: block;
    width: 40px;
    height: 25px;
    position: relative;
    z-index: 99;
  }
  .burger span, .burger::before, .burger::after {
    position: absolute;
    height: 10%;
    width: 100%;
    left: 0;
    border-radius: 2px;
    transition: all 0.3s;
    background-color: #1D2130;
  }
  .burger::before {
    content: "";
    top: 0;
  }
  .burger::after {
    content: "";
    bottom: 0;
  }
  .burger span {
    top: 50%;
    transform: scale(1) translateY(-50%);
  }
  .burger.open::before {
    top: 50%;
    transform: rotate(-45deg);
  }
  .burger.open::after {
    top: 50%;
    transform: rotate(45deg);
  }
  .burger.open span {
    transform: scale(0);
  }
}
@media (max-width: 575px) {
  .nav__list-item {
    font-size: 25px;
  }
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  height: 100vh;
  width: 100vw;
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transition: all 0.5s;
}
.popup.open {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
}
.popup__menu {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: all 0.5s;
  width: 550px;
  background: #fff;
  border-radius: 8px;
  padding: 48px;
}
.popup__menu.open {
  transform: translate(-50%, -50%) scale(1);
  transition: all 0.5s;
}
.popup__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 140%;
  color: #1D2130;
}
.popup__subtitle {
  font-weight: 400;
  font-size: 14px;
  line-height: 160%;
  margin-bottom: 28px;
}
.popup__form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr 1fr 3fr 1fr;
  gap: 24px;
}
.popup__form label {
  display: block;
  font-weight: 700;
  font-size: 12px;
  color: #1D2130;
  opacity: 0.6;
  margin-bottom: 8px;
}
.popup__form input {
  width: 100%;
  border-bottom: solid 2px #E5E5E5;
  padding-left: 3px;
  padding-bottom: 4px;
}
.popup__form input::placeholder {
  opacity: 0.3;
}
.popup__form input:focus {
  border-color: #b7b7b7;
}
.popup__form-email {
  grid-column: 1/-1;
}
.popup__form #message {
  border: solid 2px #E5E5E5;
  border-radius: 4px;
  padding: 20px 0 110px 20px;
}
.popup__form #message:focus {
  border-color: #b7b7b7;
}
.popup__form-message {
  grid-column: 1/-1;
}
.popup__close {
  position: absolute;
  top: 20px;
  right: 20px;
  background: #1D2130;
  width: 32px;
  height: 32px;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
}
.popup__close:hover {
  background: #4a4d59;
}
.popup .btn {
  width: 170px;
}
.popup__donation {
  display: flex;
  justify-content: space-around;
}
.popup__donation-link {
  min-width: 40px;
  transition: all 0.3s;
}
.popup__donation-link:hover {
  transform: scale(1.15);
}

@media (max-width: 575px) {
  .popup__menu {
    width: 95vw;
    padding: 30px;
  }
}
@media (max-width: 480px) {
  .popup__form {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 2fr 1fr;
    gap: 16px;
    max-height: 450px;
  }
  .popup__title {
